<template>
  <div class="main">
    <a-breadcrumb>
      <a-breadcrumb-item>首页</a-breadcrumb-item>
      <a-breadcrumb-item>零部件管理</a-breadcrumb-item>
      <a-breadcrumb-item>高级匹配查询</a-breadcrumb-item>
    </a-breadcrumb>
    <h2 class="title">高级匹配查询</h2>
    <div class="search_box">
      <span>选择字段：</span>
      <a-select showSearch  placeholder="选择字段名称" optionFilterProp="children" style="width: 200px;margin-right:30px" @change="handleChange" :filterOption="filterOption"  v-model="selectValue">
          <a-select-option :value="item.dataKey" v-for="item in selectList" :key="item.dataKey">
              {{item.dataName}}
          </a-select-option>
      </a-select>
      <span>对应字段条件：</span>
      <a-input placeholder="请输入对应字段条件" v-model="searchtitle"  style="margin-right:30px"/>
      <a-button @click="addExceptPrice">加入查询条件</a-button>
      <div style="width:100%;margin-bottom:10px"></div>
      <span>已选条件：</span>
      <dl class="changeseleted">
          <dt>已选({{ele}})
            <a-popconfirm
                title="确定清空数据吗?"
                @confirm="() =>  deleteExceptPriceAll()"
                cancelText="取消"
                okText="确定">
                <span>清空</span> 
                </a-popconfirm>
          </dt>
          <div style="height:100px; overflow:auto">
            <dd v-for="(item, index) in contacts" :key="index" style="margin-top: 10px">
              <div v-model="items[index]" style="float: left">
                 <span style="width: 60px">{{item.title}}:</span> <span style="width: 60px">{{item.value}}</span>
              </div>
              <a-popconfirm
                title="确定删除数据吗?"
                @confirm="() =>  deleteExceptPrice(index,item.index,item.title)"
                cancelText="取消"
                okText="确定">
                <a-icon type="close" />
                </a-popconfirm>
              <div class="clear"></div>
          </dd>
          </div>
      </dl>
      <a-button type="primary" @click="getProductLists">开始查询</a-button>
    </div>
    <div class="content_box" style="padding:0px 20px">
      <a-layout>
        <a-layout-content>
            <div class="content_title">
              <h3>高级匹配列表</h3>
            </div>
            <div class="content_table">
              <a-layout style="height:calc(100vh - 500px);padding: 0">
                  <a-table
                  :columns="columns"
                  :dataSource="productdata"
                  :pagination="showPagination"
                  :rowKey="(v,i)=>{return i}"
                 
                  >
                  <template slot="xiangsidu" slot-scope="text, record">
                      <div style="width: 170px">
                          <a-progress :percent=text size="small" />
                      </div>
                  </template>
                  <template slot="productName" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="productBrand" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="productSpecs" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="productMaterialQuality" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="productImprint" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="productLength" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="productDescribe" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="packStandards" slot-scope="text, record">
                      <span class="lineEllipsis2">{{text}}</span>
                  </template>
                  <template slot="productCode" slot-scope="text,record">
                      <span class="lineEllipsis2">
                      <a href="javascript:;" @click="opp(record)">{{text}}</a>
                      </span>
                  </template>
                  <template slot="productChart" slot-scope="text,record">
                      <span class="lineEllipsis2">
                      <a href="javascript:;" @click="$refs.preImgs.init(record)">{{text}}</a>
                      </span>
                  </template>
                  </a-table>
              </a-layout>
              <a-layout>
                  <div class="pagination-box">
                  <a-pagination
                      style="margin-left:10px"
                      showSizeChanger
                      :showTotal="total => `共 ${pageTotal} 条`"
                      showQuickJumper
                      :pageSize.sync="pageSize"
                      @showSizeChange="onShowSizeChange"
                      :total="pageTotal"
                      @change="onChange"
                      v-model="current"
                  />
                  </div>
              </a-layout> 
            </div>
        </a-layout-content>
      </a-layout>
    </div>
      <preImgs ref="preImgs"></preImgs>
  </div>
</template>
<script>
 import {deletProduct,getProductList,deletClassification,DataDictionaryDataList,getSimilar} from '@/api/ProductApi';
 import preImgs from '@/views/products/imgs_preview'
const columns = [
   {
    title: "相似度",
    dataIndex: "xiangsidu",
    key: "xiangsidu",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "xiangsidu" }
  },
  {
    title: "零部件编码",
    dataIndex: "productCode",
    key: "productCode",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productCode" }
  },
  {
    title: "零部件描述",
    dataIndex: "productDescribe",
    key: "productDescribe",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productDescribe" }
  },
  {
    title: "品名",
    dataIndex: "productName",
    key: "productName",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productName" }
  },
  {
    title: "品牌",
    dataIndex: "productBrand",
    key: "productBrand",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productBrand" }
  },
  {
    title: "规格",
    dataIndex: "productSpecs",
    key: "productSpecs",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productSpecs" }
  },
  {
    title: "材质",
    dataIndex: "productMaterialQuality",
    key: "productMaterialQuality",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productMaterialQuality" }
  },
  {
    title: "印记",
    dataIndex: "productImprint",
    key: "productImprint",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productImprint" }
  },
  {
    title: "图纸",
    dataIndex: "productChart",
    key: "productChart",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productChart" }
  },
  {
    title: "长度",
    dataIndex: "productLength",
    key: "productLength",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productLength" }
  },
  {
    title: "包装规格",
    dataIndex: "packStandards",
    key: "packStandards",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "packStandards" }
  }
];

    export default {
    data() {
        return {
           selectList:"",
           selectValue:"",
           contacts:[],
           Title1: "螺栓",
           productImage:"http://cbu01.alicdn.com/img/ibank/2018/784/537/8825735487_772937239.230x230.jpg",
           searchkey:"",
           searchtitle:"",
           importFileList: [],
            visible: false,
            addClassificationForm: this.$form.createForm(this),
            editClassificationForm:this.$form.createForm(this),
            addClassificationVisible: false,
            editClassificationVisible: false,
            confirmLoading: false,
             showPagination:false,
                data:[],
                pageSize: 20,
                current: 1,
                pageTotal: 0,
                productdata: [],
                columns,
                curparentId:0,
                curclaId:0,
                loading: false,
                items: [0],
                items2:
                  {
                    index:0,
                    title: "",
                    value: ""
                },
                ele: 0,
                shengele: '50',
        }
    }, 
  computed: {
      fileImportUrl() {
        return this.$global.baseUrl + '/classification/import-excel' + '?access_token=' + this.$global.access_token;
      },
       fileImportUrl2() {
        return this.$global.baseUrl + '/classification/create-excel' + '?access_token=' + this.$global.access_token;
      },
      
    },
   created(){
        DataDictionaryDataList({ dataType: "productParts" }).then(res => {
            this.selectList= res.data.data;
        });
    },
   methods: {
        //点击添加dom节点
        addExceptPrice() {
            if(this.searchtitle==''){
                this.$message.error("查询值不能为空");
            }else{
                const selectOne= this.selectList.filter(item =>{return item.dataKey === this.selectValue})[0];
                const selectItem={};
                selectItem.index=selectOne.dataKey;
                selectItem.title=selectOne.dataName;
                selectItem.value=this.searchtitle;
                const contact = this.contacts.filter(item =>{return item.index === selectItem.index});
                if(contact.length==0){
                    this.contacts.push(selectItem);
                }else {
                    this.contacts.filter(item =>{return item.index === selectItem.index})[0].value=this.searchtitle;
                }
                this.ele = this.contacts.length;
            }

        },
        //删除一组条件
        deleteExceptPrice(index1,index2,Ttitle) {
          if(Ttitle != ""){
            this.ele --;
            this.contacts.splice(index1, 1);
          }
        },
        //删除所有条件
        deleteExceptPriceAll() {
            this.contacts=[];
            this.ele = 0;
        },
        //获取主零部件列表
        getProductLists() {
            const product={};
            this.contacts.forEach(
                item => {
                    const index=item.index;
                    const value=item.value;
                    product[index]=item.value;
                }
            )
            getSimilar(product).then(
                res => {
                    if (res.data.code === this.$global.successCode) {
                        this.productdata = res.data.data;
                        this.pageTotal = res.data.data.length;
                        if(this.pageTotal==0){
                            this.$message.error("未找到相似零部件");
                        }
                    } else {
                        this.$message.error(res.data.message);
                    }
                },
                function(error) {
                    console.log("请求数据失败", error);
                }
            );
       },
       onChange(current) {
        this.current = current;
        //console.log(this.current)
        this.getProductLists(this.selectedKeys);
        },
        onShowSizeChange(current, pageSize) {
        this.pageSize = pageSize;
        //console.log(this.pageSize)
        this.getProductLists(this.selectedKeys);
        },

      opp(record) {
        let pane = {};
        (pane.title = "零部件" + record.productId + "-详情"),
            (pane.content = record.productName + "-详情"),
            (pane.type = 1);
        pane.key = record.productId;

        this.$emit("taboperation", pane);
      },

      getForwardNode(treedata, level){
        for(let i = 0; i < treedata.length; i++){
          treedata[i].level = level + 1;
          if(treedata[i].children){
            this.getForwardNode(treedata[i].children, treedata[i].level);
          }
        }

      },

        handleChange (value) {
           console.log(value);
        },
        filterOption(input, option) {
            return option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
      
  		
      }
    }
</script>
<style scoped>
.title {
  font-size: 18px;
  padding: 0px 20px 15px;
}
.ant-breadcrumb {
  padding: 0;
  position: absolute;
  top: 10px;
  right: 20px;
}
.changeseleted{width:490px;display: inline-block;border:1px solid #ddd;border-radius: 3px; padding: 10px;margin-right: 30px}
.changeseleted dt{color: #000; font-weight: bold}
.changeseleted dd,dl{width: 100%;text-align: left; line-height: 26px}
.changeseleted dt span{float:right; color: #00aaee;cursor: pointer; margin-right:15px;}
.changeseleted dd i{float:right; color: #00aaee;cursor: pointer; margin-right:15px;}
</style>
