<template>
  <div>
    <a-tabs
      hideAdd
      v-model="activeKey"
      type="editable-card"
      @edit="onEdit"
      style="margin-top: 5px;"
      class="tabtilt"
    >
      <a-tab-pane v-for="(pane,i) in panes" :tab="pane.title" :key="i" :closable="!pane.close">
        <list v-if="pane.close" ref="prolist" @taboperation="addTab"></list>
        <detail v-else-if="pane.type==1" :productId="pane.id" @getProduct="$refs.prolist[0].getProductLists"></detail>
        <!--@getProduct="$refs.prolist[0].getProductLists"的作用是在新增子组件里面，新增成功了以后需要调用list页面的获取产品列表的方法刷新list页面的数据，同时要在list定义 ref="prolist" -->
        <add v-else-if="pane.type==3" @close="remove(i)"  @getProduct="$refs.prolist[0].getProductLists"></add>
        <query v-else-if="pane.type==4" @taboperation="addTab"></query>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>
<script>
// import detail from "@/views/products/product_detail";
import detail from "@/components/ProductInfo.vue";
import list from "@/components/ProductList.vue";
import add from "@/components/AddProducts.vue";
import query from "@/components/MatchingQuery.vue";
export default {
  components: {
      list,
      detail,
      add,
      query
  },
  data() {
    return {
      activeKey: 0,
      panes: [{ title: "零部件管理", close: true }]
    };
  },
  mounted(){
  },
  methods: {
    addTab(productInfo) {
      if (this.panes.some(v => v.id == productInfo.key)) {
        this.activeKey = this.panes.findIndex(v => v.id == productInfo.key);
      } else {
        this.panes.push({
          title: productInfo.title,
          id: productInfo.key,
          type:productInfo.type
        });
        this.activeKey = this.panes.length - 1;
      }
    },
    onEdit(targetKey, action) {
      console.log(arguments);
      if(action=='remove')
      this[action](targetKey);
    },
    remove(targetKey) {
      let { panes,activeKey } = this;
      this.panes = panes.filter((v, i) => i != targetKey);
      this.activeKey=0;
    }
  }
};
</script>
<style scoped>
.ant-tabs-bar {
  margin: 0px;
}
</style>
